

import toastTemp from './src/toast.vue'

let Toast = {};  // 

Toast.install = (Vue, options) => { //Vueinstallvue
    // loading DOM
    if (document.getElementsByClassName('.lumos-toast').length) return

    // VUE
    let lTemp = Vue.extend(toastTemp);

    // DOMVue
    // $vm.$el
    // vm.$mount()()
    // vuedom(html, body)
    // let $vm = new toastTpl({
    //  el: document.createElement('div')
    // })

    // VUE
    let $vm = new lTemp();

    // $mount$elbody
    let tpl = $vm.$mount().$el;
    document.body.appendChild(tpl);

    Vue.prototype.$toast = function (text) {
        console.log('dsda')
        $vm.isShow = true;
        $vm.text=text;
        setTimeout(() => { $vm.isShow = false },1500);
    }
}
// //Load
export default Toast;


