


import confirmTemp from './src/confirm.vue'

let Confirm = {};  // 

Confirm.install = (Vue, options) => { //Vueinstallvue
    // loading DOM
    if (document.getElementsByClassName('.lumos-confirm').length) return

    // VUE
    let lTemp = Vue.extend(confirmTemp);

    // DOMVue
    // $vm.$el
    // vm.$mount()()
    // vuedom(html, body)
    // let $vm = new toastTpl({
    //  el: document.createElement('div')
    // })

    // VUE
    let $vm = new lTemp();

    // $mount$elbody
    let tpl = $vm.$mount().$el;
    document.body.appendChild(tpl);

    let defaultOptions = {
        title:'',
        yesBtnText: '',
        noBtnText: '',
        isShow:true
    };

    const confirm = function (options) {
        Object.assign($vm, defaultOptions, options, {
            type: 'confirm'
        });
    };

    Vue.prototype.$confirm=confirm;

    // Vue.prototype.$loading = { //Vue
    //     show() {
    //       $vm.isShow = true;
    //     },
    //     hide() {
    //         $vm.isShow = false;
    //     }
    // }
}
// //Load
export default Confirm;




