export const actions = {
	//ID
	setAId({ commit }, val) {
		commit('SETAID', val);
	},
	//
	setUId({ commit }, val) {
		commit('SETUID', val);
	},
	//
	setMessageBox({ commit }, val) {
		commit('SETMESSAGEBOX', val);
	},
	//
	setUserInfo({ commit }, val) {
		commit('SETUSERINFO', val);
	}

}


