<template>
  <div>
    <p></p>
    <p>{{ text.name }}</p>
    <!-- <p><button @click="onChange"></button></p> -->
    <hr>
    <child v-model="text.name"></child>
  </div>
</template>
<script>

export default {
  data() {
    return {
      text:{
        name:"''"
      }
    }
  },
  methods: {
    onChange() {
      this.text.name = ''
    }
  }
}
</script>
<style scoped>
</style>



