

import loadTemp from './src/loading.vue'

let Load = {};  // 

Load.install = (Vue, options) => { //Vueinstallvue
    // loading DOM
    if (document.getElementsByClassName('.lumos-loading').length) return

    // VUE
    let lTemp = Vue.extend(loadTemp);

    // DOMVue
    // $vm.$el
    // vm.$mount()()
    // vuedom(html, body)
    // let $vm = new toastTpl({
    //  el: document.createElement('div')
    // })

    // VUE
    let $vm = new lTemp();

    // $mount$elbody
    let tpl = $vm.$mount().$el;
    document.body.appendChild(tpl);

    Vue.prototype.$loading = { //Vue
        show() {
          $vm.isShow = true;
        },
        hide() {
            $vm.isShow = false;
        }
    }
}
// //Load
 export default Load;


