<template>
  <div>
    <p></p>
    <p>{{ myValue }}</p>
    <p><button @click="onChange"></button></p>
  </div>
</template>
<script>
export default {
  name:"child",
  props: {
    //value
    value: {
      type: String
    }
  },
  data() {
    return {
      myValue: this.value
    }
  },
  methods: {
    onChange() {
      this.myValue = ''
    }
  },
  watch: {
    //propvaluemyValue
    value(newVal) {
      this.myValue = newVal
    },
    //myValue
    //https://cn.vuejs.org/v2/guide/components-custom-events.html#%E8%87%AA%E5%AE%9A%E4%B9%89%E7%BB%84%E4%BB%B6%E7%9A%84-v-model
    myValue(newVal) {
      this.$emit('input', newVal)
    }
  }
}
</script>
<style scoped>
</style>



